@import url("https://fonts.googleapis.com/css2?family=Charis+SIL:ital,wght@0,400;0,700;1,700&family=Dancing+Script:wght@700&family=Josefin+Sans&family=Lobster&family=Oxygen+Mono&family=Oxygen:wght@300;400;700&family=Poppins:ital,wght@0,900;1,900&family=Shizuru&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  background-color: #F4E2DE;
  height: 100vp;
}

header {
  display: flex;
  justify-content: space-between;
  padding: 30px;
  background-color: #F4E2DE;
  box-shadow: 0px 0px 5px #FFB9AB;
}
header #logo {
  width: 200px;
  margin-top: -60px;
  margin-left: -40px;
  z-index: 1;
}
header .navbar ul {
  position: relative;
  list-style-type: none;
  padding: 0;
  margin: 0;
  height: 100px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  text-transform: uppercase;
}
header .navbar ul li {
  display: block;
  position: relative;
}
header .navbar ul li a {
  text-decoration: none;
  font-size: 1rem;
  font-weight: 600;
  color: #850818;
}
header .navbar ul li a:hover {
  font-size: 1.1rem;
  transition: 0.3s ease;
}
header .navbar ul li a.active::after, header .navbar ul li a:hover::after {
  content: " ";
  position: absolute;
  bottom: -6px;
  left: 0px;
  width: 110%;
  height: 3px;
  border-radius: 50%;
  background-color: #850818;
  opacity: 0.5;
}
header .navbar ul li:nth-child(6) a span {
  font-size: 30px;
}

section {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(12, 1fr);
  margin-top: 1rem;
}
section .front {
  grid-column: 2/7;
  grid-row: 1/12;
  order: 1;
  display: grid;
  justify-content: flex-start;
  align-items: flex-end;
  background-color: #FFF9F7;
  line-height: 150%;
  font-size: large;
}
section .front .center {
  padding-left: 3em;
  padding-bottom: 3em;
}
section .front .center a {
  position: relative;
  display: inline-block;
  padding: 10px 30px;
  text-decoration: none;
  text-transform: uppercase;
  color: black;
}
section .front .center a span {
  display: block;
  position: absolute;
  background: black;
}
section .front .center a span:nth-child(1) {
  left: 0;
  top: 0;
  width: 2px;
  height: 100%;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.5s;
}
section .front .center a span:nth-child(2) {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.5;
}
section .front .center a:hover span:nth-child(1) {
  transform: scaleY(1);
  transform-origin: bottom;
  transition: transform 0.5s;
}
section .front .center a:hover span:nth-child(2) {
  transform: scaleX(1);
  transform-origin: right;
  transition: transform 0.5s;
}
section .front-cover {
  grid-column: 6/12;
  grid-row: 2/13;
  order: 2;
  top: 50%;
}
section .front-cover img {
  width: 100%;
  box-shadow: 0 0px 10px 10px #F4E2DE;
}

.call {
  background-color: yellow;
  border: 2px;
}